.home-page-container{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
}

.title-div{
    border-bottom: 2px solid black;
}


.horizontal-scroller-div{
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    width: 90%;
    height: 350px;
    border: 3px solid black;
    margin: 3%;
    padding-top: 2%;
    border-radius: 10px;
    background-color: beige;
}