.navbar{
    width: 100%;
    height:100px;
    background-color: darkgoldenrod;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.navbar-tile{
    width: 30%;
    height:100px;
    background-color: darkgoldenrod;
    color: black;
    display: flex;
    font-size:xx-large;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.navbar-tile:hover{
    background-color: rgb(215, 171, 59);
    color: white;
    cursor: pointer;
}

h1{
    margin-left: 1%;
}
h2{
    margin-left: 2%;
}